<template>
	<u-upload 
	:action="action" 
	:max-size="5 * 1024 *1024"
	max-count="1"
	:before-upload="beforeUpload"
	:custom-btn="true"
	:show-progress="false"
	:form-data="formData"
	multiple
	:deletable="false"
	:previewFullImage="false"
	width="0"
	height="0"
	ref="upload"
	@on-success="upSuccess"
	>
	<u-avatar slot="addBtn" :src="vuex_user.avatar_url" size="120"></u-avatar>
	</u-upload>
</template>

<script>
	let _this={}
	export default {
		name:"oss-upload",
		data() {
			return {
				fileList:[],
				action:"http://www.example.com",
				formData:{},
				upFileName:""
			};
		},
		created() {
			// wx小程序中this会在上传前钩子内丢失
			_this=this
		},
		
		methods:{
			// 上传前的钩子
			async beforeUpload(index,list){
				// 设置唯一文件名
				const {file}=list[0];
				
				// #ifdef H5
				const fileName=file.name
				console.log(fileName)
				//  #endif
				// 小程序拿的是path
				// #ifndef H5
				const fileName=file.path
				//  #endif
				
				// 处理唯一文件名
				const suffix=fileName.slice(fileName.lastIndexOf("."));
				const upFileName=_this.$u.guid(20)+suffix;
				
				console.log(upFileName)
				_this.upFileName=upFileName;
				// 请求获取oss-token的api
				const oss= await _this.$u.api.authOss();
				
				// 参数准备,设置上传域名
				_this.action=oss.host
				// 额外的上传参数
				_this.formData={
					'key' : upFileName, //上传后的文件名
					'policy': oss.policy,
					'OSSAccessKeyId':oss.accessid, 
					'success_action_status' : '200', //让服务端返回200,不然，默认会返回204
					'signature': oss.signature,
				}
				return true;
			},
			// 上传成功后发射事件给父组件 (带图片key参数)
			async upSuccess(){
				// 移除预览去图片
				_this.$refs.upload.remove(0);
				// 请求接口更新头像
			   await _this.$u.api.authAvatar({avatar:_this.upFileName});
			   	
			   // 更新缓存的用户信息
			  await _this.$u.utils.setUserInfo();
			   // 弹信息
			   _this.$u.toast('更新成功');
						   		   
			}
		},
		
	}
</script>

